import Figure from 'react-bootstrap/Figure';
import 'bootstrap/dist/css/bootstrap.css';
import { Link } from "react-router-dom";


function Wildlife() {
    return (
        <div>
            <h1>Wildlife</h1>
            
            <Link to="/Frog">
            <Figure style={{padding: "10px"}}>
                <Figure.Image
                    width={125}
                    height={125}
                    src="https://github.com/brettbookbinder/gathertown/raw/master/fossil2%20(5).png"/>
                <Figure.Caption>Posion Dart Frog</Figure.Caption>
            </Figure>
            </Link>
        </div>
    );
}

export default Wildlife;